import React, { useState, useEffect } from 'react';
import axios from 'axios';

export const CreatePost = () => {
    const [title, setTitle] = useState('');
    const [text, setText] = useState('');
    const [image, setImage] = useState('');
    const [tags, setTags] = useState([]);
    const [tagInput, setTagInput] = useState('');
    const [allTags, setAllTags] = useState([]);

    useEffect(() => {
        // Функция для получения всех тегов с сервера
        const fetchTags = async () => {
            try {
                const response = await axios.get('https://04cb5470549a62ec.mokky.dev/tags');
                setAllTags(response.data); // Обновляем состояние со списком всех тегов
            } catch (error) {
                console.error('Error fetching tags:', error);
            }
        };

        fetchTags(); // Вызываем функцию при загрузке компонента
    }, []);

    const handleTagInputChange = (e) => {
        setTagInput(e.target.value);
    };

    const handleAddTag = () => {
        setTags([...tags, tagInput.trim()]);
        setTagInput('');
    };

    const handleRemoveTag = (tag) => {
        setTags(tags.filter((t) => t !== tag));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const response = await axios.post('https://your-api-url/posts', {
                title,
                text,
                image,
                tags,
            });
            console.log('Post created:', response.data);
            // Reset form fields after successful submission
            setTitle('');
            setText('');
            setImage('');
            setTags([]);
        } catch (error) {
            console.error('Error creating post:', error);
        }
    };

    return (
        <div>
            <h2>Create Post</h2>
            <form onSubmit={handleSubmit}>
                <div>
                    <label>Title:</label>
                    <input type="text" value={title} onChange={(e) => setTitle(e.target.value)} />
                </div>
                <div>
                    <label>Text:</label>
                    <textarea value={text} onChange={(e) => setText(e.target.value)} />
                </div>
                <div>
                    <label>Image URL:</label>
                    <input type="text" value={image} onChange={(e) => setImage(e.target.value)} />
                </div>
                <div>
                    <label>Tags:</label>
                    <div>
                        <input type="text" value={tagInput} onChange={handleTagInputChange} />
                        <button type="button" onClick={handleAddTag}>Add Tag</button>
                    </div>
                    <div>
                        {allTags.map((tag, index) => (
                            <span key={tag.id}>
                                {tag.name}
                                <button type="button" onClick={() => handleAddTag(tag)}>Add</button>
                            </span>
                        ))}

                    </div>
                    <div>
                        {tags.map((tag, index) => (
                            <span key={index}>
                                {tag}
                                <button type="button" onClick={() => handleRemoveTag(tag)}>Remove</button>
                            </span>
                        ))}
                    </div>
                </div>
                <button type="submit">Create Post</button>
            </form>
        </div>
    );
};
